@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
button {
	border: none;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Lato', sans-serif;
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.037);
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
  font: inherit;
}

button {
  cursor: pointer;
}

.hidden {
  position: absolute;
  left: -100000px;
  height: 0;
}

.page-container {
  margin-top: 5rem;
}

.page-title {
  font-size: 2rem;
  padding: 2rem;
}

.homepage-content {
  padding: 0 2rem;
}

.quote-page-content {
  margin-top: 2rem;
  padding: 0 2rem;
}

.Display-container {
  --display-bg: #72788d;
  --display-font-color: #f7f7f7;

  grid-area: display;
  background-color: var(--display-bg);
  color: var(--display-font-color);
}

.Display-display {
  text-align: end;
  font-size: 3rem;
  padding: 2rem 0.5rem;
}

.Other-Operations-container {
  --buttons-gray: #efefef;

  grid-area: other-operation;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3px;
  gap: 3px;
}

.Other-Operations-btn {
  background-color: var(--buttons-gray);
}

.Digits-container {
  --buttons-gray: #efefef;
  --buttons-font-color: #191919;

  grid-area: digit;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3px;
  gap: 3px;
}

.Digits-btn {
  background-color: var(--buttons-gray);
  color: var(--buttons-font-color);
}

.zero-btn {
  grid-column: span 2;
}

.Operators-container {
  --buttons-orange: #e79650;
  --buttons-font-color: #191919;

  grid-area: operation;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: row;
  grid-gap: 3px;
  gap: 3px;
}

.Operators-btn {
  background-color: var(--buttons-orange);
  color: var(--buttons-font-color);
}

.Equal-container {
  --buttons-orange: #e79650;
  --buttons-font-color: #191919;

  grid-area: equal;
}

.Equal-btn {
  background-color: var(--buttons-orange);
  color: var(--buttons-font-color);
  width: 100%;
}

/* color scheme: https://coolors.co/e79650-191919-f7f7f7-efefef-72788d */

:root {
  --main-bg-color: #f7f7f7;
  --display-bg: #72788d;
  --display-font-color: #f7f7f7;
  --buttons-orange: #e79650;
  --buttons-gray: #efefef;
  --buttons-font-color: #191919;
}

.calculator-page-container {
  position: relative;
}

.calculator-page-title {
  position: absolute;
}

.Calculator-container {
  box-shadow: 0 5px 15px 10px rgba(114, 120, 141, 0.2);
  max-width: 600px;
  margin: 0 auto;
  background-color: #f7f7f7;
  background-color: var(--main-bg-color);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-template-areas:
    "display display display display"
    "other-operation other-operation other-operation operation"
    "digit digit digit operation"
    "digit digit digit operation"
    "digit digit digit operation"
    "digit digit digit equal";
  grid-gap: 3px;
  gap: 3px;
}

.btn {
  font-size: 1rem;
  padding: 2rem 3rem;
}

/*
  ========================

  Media Queries

  ========================
*/

/* Large devices (laptops/desktops, 768px and up) */
@media only screen and (min-width: 769px) {
  .btn {
    font-size: 2rem;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.main-title {
  font-size: 5rem;
  padding: 2rem 0;
}

.navbar-links {
  display: flex;
  align-items: center;
}

.navbar-link-li:nth-child(1) {
  padding-right: calc(1rem + 1px);
}

.navbar-link-li:nth-child(2) {
  border-left: solid 1px black;
  border-right: solid 1px black;
  padding: 1rem calc(1rem + 1px);
}

.navbar-link-li:nth-child(3) {
  padding-left: calc(1rem + 1px);
}

.navbar-link {
  font-size: 1.5rem;
  color: inherit;
}

.navbar-link:hover {
  font-size: 1.5rem;
  background-color: #e79650;
}

.active-link {
  color: white;
  background-color: #72788d;
}



